    "第二項": "認知、情緒行為、學習、社會適應、人際互動、注意力、活動量、衝動性",
    "第三項": "語言（理解、溝通、構音、口吃）"
}

const checkedArr: Ref<number[]> = ref([])

function checkedArrClick() {
    console.log(checkedArr.value)

    if (checkedArr.value.length >= 2) {
        step.value = 90;
        return;
    }
    if (checkedArr.value.length <= 0) {
        step.value = 80;
        return;
    }

    if (checkedArr.value[0] == 0) {
        step.value = 101
        return;
    }
    if (checkedArr.value[0] == 1) {
        step.value = 102
        return;
    }
    if (checkedArr.value[0] == 2) {
        step.value = 101
        return;
    }
}

</script>

